import { sendList } from "../chrome/utils";
import { StorageKey } from "../background/const";
import { getVideoObject } from "../utils";
import { VideoType } from "../types/MediaTypes";
import { ActionType, ChromeMessageType } from "../background/actions/types";

const getData = function () {
  const currentLocation = window.location;
  const videoId = currentLocation.pathname;

  chrome.storage.local.get([StorageKey.VimeoToken], res => {
    const token: string = res[StorageKey.VimeoToken] ?? "";
    if(token) {
      const headers = {
        'Authorization': token,
        'Content-Type': 'application/json'
      };

      const url = `https://api.vimeo.com/videos/${videoId}`;

      fetch(url, {headers})
          .then((response) => {
            if (response.ok) {
              return response.json();
            } else {
              throw new Error(`HTTP error: ${response.status}`);
            }
          })
          .then((data) => {
            const list: VideoType[] = [];
            const promisesList: Promise<void>[] = [];
            data?.files.forEach((file: { link: string; size: number; type: string; expires: string; created_time: string; }) => {
              const promise = getVideoObject(data?.name, file?.link, {
                'content-length': `${file?.size}`,
                'content-type': file?.type,
                'expires': file?.expires,
                'last-modified': file?.created_time,
              }).then(obj => {
                list.push(obj)
              })
              promisesList.push(promise)
            })
            Promise.all(promisesList).then(() => {
              sendList({
                list,
                header: data?.name,
                id: data?.link,
              });
            })
          })
          .catch((error) => {
            console.error(error);
          });
    }
    else {
      chrome.storage.onChanged.addListener(() => {
        getData();
      });

      chrome.runtime.sendMessage<ChromeMessageType>({
        action: ActionType.Error,
        error: 'Login on vimeo to download from this site',
      })
    }
  })
};

getData();

chrome.runtime.onMessage.addListener((message) => {
  if (message.action === "pageUpdated") {
    getData();
  }
  return true;
});
